import { HashRouter as Router, Switch, Route } from 'react-router-dom';

import Main from './pages/Main/Main';
import About from './pages/About/About';
import FreeAd from './pages/FreeAd/FreeAd';

import './App.css';

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/" exact>
          <Main />
        </Route>
        <Route path="/about">
          <About />
        </Route>
        <Route path="/admission">
          <FreeAd />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
