import React from 'react';
import Header from '../../components/Header/Header';
import AsideText from './AsideText/AsideText';
import Footer from './Footer/Footer';

import './FreeAd.css';

const FreeAd = () => {
  return (
    <div className="FreeAd">
      <div className="FreeAd__Wrapper"></div>

      <Header color="black" brandName={true} />

      <div className="FreeAd__Main">
        <aside>
          <AsideText />
          <Footer />
        </aside>

        <aside></aside>
      </div>
    </div>
  );
};

export default FreeAd;
