import React from 'react';
import Logo from '../Logo/Logo';
import Links from './Links/Links';
import Text from './Text/Text';

const Footer = () => {
  return (
    <div className="FreeAd__Footer">
      <div className="FreeAd__Footer__Wrapper">
        <Logo />
        <Text />
      </div>
      <Links />
    </div>
  );
};

export default Footer;
